/**
 * id, label, value   CheckedItem  .
 * onDelete (id) => {}      , id  parameter  .
 * @param { id, label, value onDelete } param
 * @returns JSX.Element
 */
export const CheckedItem = ({ id, label, value, onDelete }) => {
  return (
    <div className="ez-checkeditem">
      {label}
      <span className="deleteIcon" onClick={() => onDelete(id)}>
        {' '}
        X{' '}
      </span>
    </div>
  );
};

/**
 * items  onDelete   CheckedList  .
 * items  { id, label, value }  .
 * items   "  ."   .
 * @param { items { id, label, value }[], onDelete: (idDeleted) => {} } param0 
 * @returns JSX.Element
 */
export const CheckedList = ({ items, onDelete }) => {
  if (items.length === 0) {
    return <div className="checked-list">  .</div>;
  }

  return (
    <div className="ez-checkedlist">
      {items.map(item => (
        <CheckedItem
          key={item.id}
          id={item.id}
          label={item.label}
          onDelete={onDelete}
        />
      ))}
    </div>
  );
};
