import { useState, useMemo } from 'react';
import LabelEditable from '../edit/LabelEditable';
import ErrorMessage from '../edit/ErrorMessage';

const EMAIL_MAX_LENGTH = 50;
const PASSWORD_MAX_LENGTH = 50;
const PASSWORD_MIN_LENGTH = 8;

const checkString = validFns => value => {
  for (const fn of validFns) {
    const { isValid, msg } = fn(value);
    if (!isValid) {
      return { isValid, msg };
    }
  }
  return { isValid: true, msg: '' };
};

const checkIsTooLong = maxLength => value => {
  if (value.length > maxLength) {
    return { isValid: false, msg: `too long: ${maxLength}` };
  }
  return { isValid: true, msg: '' };
};

const checkIsTooShort = minLength => value => {
  if (value.length < minLength + 1) {
    return { isValid: false, msg: `too short: ${minLength}` };
  }
  return { isValid: true, msg: '' };
};

const checkIsEmpty = value => {
  if (value.length === 0) {
    return { isValid: false, msg: 'empty' };
  }
  return { isValid: true, msg: '' };
};

const checkIsPasswordFormat = value => {
  // Regular expression to validate password format
  const re = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;
  if (!re.test(value)) {
    return { isValid: false, msg: 'Invalid password format' };
  }
  return { isValid: true, msg: '' };
};

/**
 *        .
 * @param {*} value
 * @returns
 */
const checkIsIncludeAlphabet = value => {
  const re = /[a-zA-Z]/;
  if (!re.test(value)) {
    return {
      isValid: false,
      msg: 'Must include at least one alphabet character',
    };
  }
  return { isValid: true, msg: '' };
};

/**
 *       .
 * @param {*} value
 * @returns
 */
const checkIsIncludeNumber = value => {
  const re = /\d/;
  if (!re.test(value)) {
    return { isValid: false, msg: 'Must include at least one number' };
  }
  return { isValid: true, msg: '' };
};

const checkIsEmailFormat = value => {
  const re = /\S+@\S+\.\S+/;
  if (!re.test(value)) {
    return { isValid: false, msg: 'not email format' };
  }
  return { isValid: true, msg: '' };
};

const checkValidEmail = checkString([
  checkIsEmpty,
  checkIsTooLong(PASSWORD_MAX_LENGTH),
  checkIsEmailFormat,
]);

const checkValidPassword = checkString([
  checkIsEmpty,
  checkIsTooShort(PASSWORD_MIN_LENGTH),
  checkIsTooLong(PASSWORD_MAX_LENGTH),
  checkIsIncludeAlphabet,
  checkIsIncludeNumber,
]);

/**
 * 

: 
- email validation: length > 0, email format
- password validation; length > 0,
 * @param {*} param0 
 * @returns 
 */
export const SigninEmail1 = ({ onConfirm }) => {
  const [email, setEamil] = useState('');
  const [errMsgEmail, setErrMsgEmail] = useState('');
  const [pw, setPw] = useState('');
  const [errMsgPw, setErrMsgPw] = useState('');

  const handleChangeEmail = e => {
    const { value } = e.target;

    setEamil(value);
    const { isValid, msg } = checkValidEmail(value);
    if (isValid) {
      setErrMsgEmail('');
    } else {
      setErrMsgEmail(msg);
    }
  };

  const handleChangePw = e => {
    const { value } = e.target;

    setPw(value);
    const { isValid, msg } = checkValidPassword(value);
    if (isValid) {
      setErrMsgPw('');
    } else {
      setErrMsgPw(msg);
    }
  };

  const isActiveConfirm = useMemo(() => {
    if (email.length === 0 || pw.length === 0) return false;
    if (errMsgEmail === '' && errMsgPw === '') return true;
    return false;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [email, pw]);

  const hanldeClickConfirm = () => {
    onConfirm(email, pw);
  };

  return (
    <div className="ez-signinemail1">
      <h2>Sign In</h2>

      <div>
        <LabelEditable
          className="horizontal"
          label="EMAIL"
          type="email"
          value={email}
          onChange={handleChangeEmail}
        />
        <ErrorMessage message={errMsgEmail} />
      </div>

      <div>
        <LabelEditable
          className="horizontal"
          label="PASSWORD"
          type="password"
          value={pw}
          onChange={handleChangePw}
        />
        <ErrorMessage message={errMsgPw} />
      </div>
      <div>
        <button
          className={`btn-confirm ${isActiveConfirm ? '' : 'inactive'}`}
          onClick={hanldeClickConfirm}
        >
          signin
        </button>
      </div>
    </div>
  );
};
