/**
 *  component.     ,
 *       .      ,
 *       .
 * 
  - email 
  - email validation
  - email   
  - email   
  - password 
  - password validation
  - confirm btn 
  - confirm btn  onConfirm 
  - confirm btn /
  -      ()
 *
 * @param {function} onConfirm -     . formData   .
 * @param {function} onCheckDuplicate -       .    .
 * @param {Array} additionalFields -       .   `name`, `label`, `type`  ,        .
 */
export const SignupEmail1 = ({
  onConfirm,
  onCheckDuplicate,
  additionalFields = [],
}) => {
  const initialFormState = {
    email: '',
    pw: '',
    ...additionalFields.reduce(
      (acc, field) => ({ ...acc, [field.name]: '' }),
      {},
    ),
  };

  const [formData, setFormData] = useState(initialFormState);
  const [errMsg, setErrMsg] = useState({
    email: '',
    pw: '',
    ...additionalFields.reduce(
      (acc, field) => ({ ...acc, [field.name]: '' }),
      {},
    ),
  });
  const [isCheckedDuplicated, setIsCheckedDuplicated] = useState(false);

  const handleChange = field => e => {
    const { value } = e.target;
    setFormData(prev => ({ ...prev, [field]: value }));

    if (field === 'email') {
      const { isValid, msg } = checkValidEmail(value);
      setErrMsg(prev => ({ ...prev, email: isValid ? '' : msg }));
    } else if (field === 'pw') {
      const { isValid, msg } = checkValidPassword(value);
      setErrMsg(prev => ({ ...prev, pw: isValid ? '' : msg }));
    } else {
      const fieldInfo = additionalFields.find(f => f.name === field);
      if (fieldInfo && fieldInfo.validate) {
        const { isValid, msg } = fieldInfo.validate(value);
        setErrMsg(prev => ({ ...prev, [field]: isValid ? '' : msg }));
      }
    }
  };

  const handleCheckDuplicate = () => {
    setErrMsg(prev => ({ ...prev, email: 'checking...' }));
    onCheckDuplicate(formData.email).then(isDuplicated => {
      if (isDuplicated) {
        setErrMsg(prev => ({ ...prev, email: ' .' }));
        setIsCheckedDuplicated(false);
      } else {
        setErrMsg(prev => ({ ...prev, email: ' .' }));
        setTimeout(() => {
          setErrMsg(prev => ({ ...prev, email: '' }));
        }, 1800);
        setIsCheckedDuplicated(true);
        // check  email   .
      }
    });
  };

  const isActiveDuplicate = useMemo(
    () => formData.email.length > 0 && !errMsg.email,
    [formData.email, errMsg.email],
  );

  const isActiveConfirm = useMemo(() => {
    return (
      Object.values(formData).every(value => value) &&
      Object.values(errMsg).every(error => !error)
    );
  }, [formData, errMsg]);

  const handleClickConfirm = () => {
    if (isActiveConfirm) {
      const { email, pw, ...additionalData } = formData;
      onConfirm(email, pw, additionalData);
    }
  };

  return (
    <div className="ez-signupemail1">
      <h2>Sign Up</h2>

      <div>
        <LabelEditable
          className="horizontal"
          label="EMAIL"
          type="email"
          value={formData.email}
          onChange={handleChange('email')}
        />
        <button
          className={`btn-confirm ${isActiveDuplicate ? '' : 'inactive'}`}
          onClick={handleCheckDuplicate}
        >
            
        </button>
        <ErrorMessage message={errMsg.email} />
      </div>

      <div>
        <LabelEditable
          className="horizontal"
          label="PASSWORD"
          type="password"
          value={formData.pw}
          onChange={handleChange('pw')}
        />
        <ErrorMessage message={errMsg.pw} />
      </div>

      {additionalFields.map(field => (
        <div key={field.name}>
          <LabelEditable
            className="horizontal"
            label={field.label}
            type={field.type || 'text'}
            value={formData[field.name]}
            onChange={handleChange(field.name)}
          />
          <ErrorMessage message={errMsg[field.name]} />
        </div>
      ))}

      <div>
        <button
          className={`btn-confirm ${isActiveConfirm ? '' : 'inactive'}`}
          onClick={handleClickConfirm}
        >
          signup
        </button>
      </div>
    </div>
  );
};
