import { useNavigate } from 'react-router-dom';
import { Button } from './Button';

/**
 * PathButton 
 * @param {string} label -   
 * @param {string} path -     
 * @param {string} color -  
 * @param {string} className - Button  CSS 
 * @returns {JSX.Element}   
 */
export const PathButton = ({ label, path, color, className }) => {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate(path);
  };

  return (
    <Button
      label={label}
      onClick={handleClick}
      color={color}
      className={className}
    />
  );
};
