import React, { useState, useEffect } from 'react';
import { Modal } from './Modal';
import { Button } from '../components/Button';
import '../css/StatusModal.css';

/**
 *     
 *
 * @param {Object} props -  props
 * @param {boolean} props.isOpen -    
 * @param {Function} props.onClose -     
 * @param {Function} props.onSave -      
 * @param {string} props.initialStatus -   
 * @param {number} props.studentId -  ID
 * @param {string} props.date - 
 * @returns {JSX.Element}   
 */
export const StatusModal = ({ isOpen, onClose, onSave, initialStatus }) => {
  const [status, setStatus] = useState(initialStatus);

  useEffect(() => {
    setStatus(initialStatus);
  }, [initialStatus]);

  const handleSave = () => {
    onSave(status);
    onClose();
  };

  return (
    <Modal isOpen={isOpen}>
      <div className="status-modal-content">
        <h3 className="status-modal-header">  </h3>
        <select
          className="status-modal-select"
          value={status}
          onChange={e => setStatus(e.target.value)}
        >
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <Button
          className="status-modal-button"
          label=""
          onClick={handleSave}
          color="#007bff"
        />
        <Button
          className="status-modal-button"
          label=""
          onClick={onClose}
          color="#0072ff"
        />
      </div>
    </Modal>
  );
};
