import React, { useState, useEffect } from 'react';
import 'css/Desk.css';
import { Button } from './Button';
import { Modal } from './Modal';

/**
 *  
 * @param {number} row -  
 * @param {number} column -  
 * @param {boolean} isAttendanceStarted -   
 * @param {array} attendanceRecords -  
 * @param {number} classId -  ID
 * @param {object} currentUser -    
 * @returns {JSX.Element}  
 */
export const Desk = ({
  row,
  column,
  isAttendanceStarted,
  attendanceRecords,
  classId,
  currentUser,
}) => {
  const [showConfirmModal, setShowConfirmModal] = useState(false); //   
  const [showAlertModal, setShowAlertModal] = useState(false); //      
  const [showOccupiedModal, setShowOccupiedModal] = useState(false); //      
  const [selectedDesk, setSelectedDesk] = useState(null);
  const apiUrl = process.env.REACT_APP_API_URL;
  const [attendanceStatus, setAttendanceStatus] = useState(
    Array(row)
      .fill()
      .map(() => Array(column).fill(null)),
  );

  // attendanceRecords  attendanceStatus 
  useEffect(() => {
    const updatedStatus = Array(row)
      .fill()
      .map(() => Array(column).fill(null));

    attendanceRecords.forEach(record => {
      updatedStatus[record.deskRow][record.deskColumn] = {
        studentName: record.studentName,
        attendanceId: record.attendanceId,
        studentId: record.studentId,
      };
    });

    setAttendanceStatus(updatedStatus);
  }, [attendanceRecords, row, column]);

  const deskGrid = Array(row)
    .fill()
    .map(() => Array(column).fill());

  function handleCellClick(rowIndex, columnIndex) {
    if (!isAttendanceStarted) {
      setShowAlertModal(true);
      return;
    }
    setSelectedDesk({ rowIndex, columnIndex });
    setShowConfirmModal(true);
  }

  function handleCloseModal() {
    setShowConfirmModal(false);
    setSelectedDesk(null);
  }

  function handleCloseAlertModal() {
    setShowAlertModal(false);
  }

  function handleCloseOccupiedModal() {
    setShowOccupiedModal(false);
  }

  async function handleConfirmAttendance() {
    const existingRecord = attendanceRecords.find(
      record => record.studentId === currentUser.userId,
    );

    if (existingRecord) {
      //        
      const response = await fetch(
        `${apiUrl}/api/classes/${classId}/attendance/student`,
        {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            access: localStorage.getItem('access_token'),
          },
          body: JSON.stringify({
            attendanceId: existingRecord.attendanceId,
            deskRow: selectedDesk.rowIndex,
            deskColumn: selectedDesk.columnIndex,
          }),
        },
      );

      if (!response.ok) {
        const error = await response.json();
        if (error.message === 'Desk is already occupied') {
          setShowOccupiedModal(true);
          handleCloseModal();
        } else {
          console.log(error.message);
        }
        return;
      }
    } else {
      //    
      const response = await fetch(
        `${apiUrl}/api/classes/${classId}/attendance/student`,
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            access: localStorage.getItem('access_token'),
          },
          body: JSON.stringify({
            attendanceDate: new Date(
              new Date().getTime() + 9 * 60 * 60 * 1000,
            ).toISOString(),
            deskRow: selectedDesk.rowIndex,
            deskColumn: selectedDesk.columnIndex,
            attendanceStatus: '',
          }),
        },
      );

      if (!response.ok) {
        const error = await response.json();
        if (error.message === 'Desk is already occupied') {
          setShowOccupiedModal(true);
          handleCloseModal();
        } else {
          console.log(error.message);
        }
        return;
      }
    }

    const updatedStatus = [...attendanceStatus];
    updatedStatus[selectedDesk.rowIndex][selectedDesk.columnIndex] = {
      studentName: currentUser.name,
      attendanceId: existingRecord ? existingRecord.attendanceId : null,
      studentId: currentUser.userId,
    };

    setAttendanceStatus(updatedStatus);
    setShowConfirmModal(false);
    setSelectedDesk(null);
  }

  return (
    <div className="desk-layout">
      <div className="screen"></div>
      <div className="desk-grid">
        {deskGrid.map((deskRow, rowIndex) => (
          <div key={rowIndex} className="desk-row">
            {deskRow.map((_, columnIndex) => (
              <div key={columnIndex}>
                <Button
                  className="desk-cell"
                  label={
                    attendanceStatus[rowIndex][columnIndex]?.studentName
                      ? `${attendanceStatus[rowIndex][columnIndex].studentName}`
                      : `${rowIndex + 1},${columnIndex + 1}`
                  }
                  color={
                    attendanceStatus[rowIndex][columnIndex]?.studentName
                      ? '#4caf50'
                      : '#b1cde9'
                  }
                  onClick={() => handleCellClick(rowIndex, columnIndex)}
                />
              </div>
            ))}
          </div>
        ))}
      </div>
      {/*   */}
      <Modal isOpen={showConfirmModal} onClose={handleCloseModal}>
        <p>  ?</p>
        <div className="modal-buttons">
          <Button
            className="status-modal-button"
            label=""
            onClick={handleConfirmAttendance}
            color="#007bff"
          />
          <Button
            className="status-modal-button"
            label=""
            onClick={handleCloseModal}
            color="#ff6347"
          />
        </div>
      </Modal>
      <Modal isOpen={showAlertModal} onClose={handleCloseAlertModal}>
        <p>  .</p>
        <div className="modal-buttons">
          <Button
            className="status-modal-button"
            label=""
            onClick={handleCloseAlertModal}
            color="#007bff"
          />
        </div>
      </Modal>
      <Modal isOpen={showOccupiedModal} onClose={handleCloseOccupiedModal}>
        <p>     .</p>
        <div className="modal-buttons">
          <Button
            className="status-modal-button"
            label=""
            onClick={handleCloseOccupiedModal}
            color="#ff6347"
          />
        </div>
      </Modal>
    </div>
  );
};
