import { SignupEmail1 } from 'ezy-ui';
import 'ezy-ui/dist/sign/SignEmail1.css';
import '../css/SignPage.css';

import { useNavigate } from 'react-router-dom';

export const SignupPage = () => {
  const navigate = useNavigate();

  /**
   *       
   *
   * @param {string} email -    
   * @param {string} password -   
   * @param {string} role -   (""  "")
   * @param {string} name -   
   */
  const apiUrl = process.env.REACT_APP_API_URL;

  const handleOnSubmit = async (email, password, additionalData) => {
    try {
      const response = await fetch(`${apiUrl}/join`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username: email,
          password: password,
          role: additionalData.role,
          name: additionalData.name,
        }),
      });

      const data = await response.json();

      if (response.ok) {
        console.log(data.message);
        navigate('/');
        return;
      }

      switch (response.status) {
        case 409:
          console.error('Signup failed:', data.message);
          throw new Error('Username already exists');
        case 422:
          console.error('Signup failed:', data.message);
          throw new Error('Invalid user data provided');
        default:
          console.error('Unexpected error:', data.message);
          throw new Error('Unexpected error occurred during signup');
      }
    } catch (error) {
      console.error('Signup failed:', error.message);
      throw error;
    }
  };

  /**
   *      
   *
   * @param {string} email -     
   * @returns {Promise<boolean>}    true,    false 
   */
  const handleOnCheck = async email => {
    try {
      const response = await fetch(`${apiUrl}/join/check-username`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username: email,
        }),
      });

      const data = await response.json();
      if (response.ok) {
        return data.isDuplicated;
      } else {
        switch (response.status) {
          case 400:
            console.error('Error: ', data.message);
            throw new Error('Username is required');
          case 409:
            console.error('Error: Username is already taken.');
            return true;
          default:
            console.error('Unexpected error:', data.message);
            throw new Error('Unexpected error occurred');
        }
      }
    } catch (error) {
      console.error('Error checking email duplication:', error.message);
      throw error;
    }
  };

  const validateName = name => {
    if (name.length >= 2) {
      return { isValid: true };
    } else {
      return { isValid: false, msg: '2  .' };
    }
  };

  const validateRole = role => {
    const validRoles = ['', ''];
    if (validRoles.includes(role.toLowerCase())) {
      return { isValid: true };
    } else {
      return {
        isValid: false,
        msg: '""  "" .',
      };
    }
  };

  const additionalFields = [
    {
      name: 'role',
      label: 'Role',
      type: 'text',
      validate: validateRole,
    },
    {
      name: 'name',
      label: 'Name',
      type: 'text',
      validate: validateName,
    },
  ];

  return (
    <div className="page-container">
      <div className="custom-signin">
        <SignupEmail1
          onConfirm={handleOnSubmit}
          onCheckDuplicate={handleOnCheck}
          additionalFields={additionalFields}
        />
      </div>
    </div>
  );
};
