import React from 'react';
import { useNavigate } from 'react-router-dom';
import 'css/Card.css';

/**
 * Card 
 * @param {string} title -   
 * @param {string} color -  
 * @param {string} path -    
 * @returns {JSX.Element}    
 */
export const Card = ({ title, color, path }) => {
  const navigate = useNavigate();

  //      
  const handleClick = () => {
    navigate(path);
  };

  return (
    <div
      className="card"
      style={{ '--card-color': color }}
      onClick={handleClick}
    >
      <div className="card-title">{title}</div>
    </div>
  );
};
