import React, { useState, useEffect } from 'react';

/**
 * Timer 
 * @param {number} initialTime -    ( )
 * @param {function} onComplete -     
 * @param {number} classId -  ID
 * @param {boolean} isAttendanceStarted -   
 * @returns {JSX.Element}  
 */
export const Timer = ({
  initialTime,
  onComplete,
  classId,
  isAttendanceStarted,
}) => {
  const [timeLeft, setTimeLeft] = useState(initialTime);
  const apiUrl = process.env.REACT_APP_API_URL;

  //     
  useEffect(() => {
    let timerId;

    const fetchTimeLeft = async () => {
      const accessToken = localStorage.getItem('access_token');
      if (!accessToken) {
        console.log('Access token not found');
        return;
      }

      const response = await fetch(
        `${apiUrl}/api/classes/${classId}/attendance/timer`,
        {
          method: 'GET',
          headers: {
            access: accessToken,
          },
        },
      );

      if (response.ok) {
        const data = await response.json();
        setTimeLeft(data.secondsRemaining);
      } else {
        console.log('Failed to fetch time left');
      }
    };

    // 1    
    if (isAttendanceStarted) {
      fetchTimeLeft();
      timerId = setInterval(fetchTimeLeft, 1000);
    }

    return () => clearInterval(timerId);
  }, [classId, isAttendanceStarted, apiUrl]);

  //     
  useEffect(() => {
    if (timeLeft <= 0 && isAttendanceStarted) {
      onComplete();
    }
  }, [timeLeft, onComplete, isAttendanceStarted]);

  const formatTime = time => {
    const minutes = String(Math.floor(time / 60)).padStart(2, '0');
    const seconds = String(time % 60).padStart(2, '0');
    return `${minutes}:${seconds}`;
  };

  return (
    <div className="timer-container">
      <div className="timer">{formatTime(timeLeft)}</div>
    </div>
  );
};
