import { includes, isNotNil, isNil } from 'ramda';
import {
  ToggleOneButtonGroup,
  TOGGLE_SIZE,
} from './ToggleButton';

/**
 * id, label, value, onClick, isChecked   Checkbox  .
 * isChecked   .   .
 * @param { id, label, value, onClick, isChecked, children } param0
 * @returns JSX.Element
 */
export const Checkbox = ({
  id,
  label,
  value,
  onClick,
  isChecked,
  children,
}) => {
  const handleClick = e => {
    const newIsChecked = !isChecked;
    onClick(newIsChecked, id, value);
  };

  return (
    <div className="ez-checkbox">
      <input type="checkbox" checked={isChecked} onChange={handleClick} />{' '}
      {label}
      {children}
    </div>
  );
};

/**
 * items  itemsChecked   CheckList  .
 * itemsChecked  items      .
 * onCheck  itemsChecked  paremter   .  Checkbox     .
 * items  item  { id, label, value }    .
 * itemsChecked  id    item    Checkbox  isChecked  .
 * @param { items: { id, label, value }[], onCheck: (newItemsChecked) => {}, itemsChecked } param
 * @returns JSX.Element
 */
export const CheckListType1 = ({ items, onCheck, itemsChecked }) => {

  /**
   * Checkbox     
   *  item  itemsChecked   .
   * @param {*} isChecked
   * @param {*} itemChecked
   */
  const handleOnToggleClick = (isChecked, itemChecked) => {
    const newItemsChecked = isChecked
      ? [...itemsChecked, itemChecked]
      : itemsChecked.filter(el => el.id !== itemChecked.id);
    onCheck(newItemsChecked);
  };

  return (
    <>
      {items.map(item => (
        <Checkbox
          item={item}
          label={item.label}
          key={item.id}
          isChecked={includes(
            item.id,
            itemsChecked.map(item => item.id),
          )}
          onClick={isChecked => handleOnToggleClick(isChecked, item)}
        />
      ))}
    </>
  );
};

/**
 *
 * item: { id, label, value }
 * options1Items: { id, label, value }[];
 * options2Items: { id, label, value }[];
 * itemChecked: {
 *  isItemChecked: boolean,
 *  option1ItemChecked: ,
 *  option2ItemChecked: ,
 * }
 * onClick: (isChecked: boolean, id: number, value: string) => void;
 * @param { } param0
 * @returns
 */
export const CheckOption = ({
  item,
  option1Items,
  option2Items,
  itemChecked,
  onClick,
}) => {
  // CheckGuide.js:168 Warning: You provided a `checked` prop to a form field without an `onChange` handler. This will render a read-only field. If the field should be mutable use `defaultChecked`. Otherwise, set either `onChange` or `readOnly`.
  return (
    <div className="ez-checkoption">
      <Checkbox
        id={item.id}
        label={item.label}
        value={item.value}
        isChecked={isNotNil(itemChecked) && item.id === itemChecked.id}
        onClick={isCheckedNew => {
          if (isCheckedNew) {
            onClick({ ...item, option1: undefined, option2: undefined });
          } else {
            onClick(undefined);
          }
        }}
      />

      <div className="option1-group">
        <ToggleOneButtonGroup
          items={option1Items}
          rowCount={2}
          onClick={newItemChecked => {
            if (isNil(itemChecked)) {
              onClick(undefined);
            } else {
              onClick({ ...itemChecked, option1: newItemChecked });
            }
          }}
          itemChecked={isNil(itemChecked) ? undefined : itemChecked.option1}
          size={TOGGLE_SIZE.SMALL}
        />
      </div>

      <div className="option2-group">
        <ToggleOneButtonGroup
          items={option2Items}
          rowCount={3}
          onClick={newItemChecked => {
            if (isNil(itemChecked)) {
              onClick(undefined);
            } else {
              onClick({ ...itemChecked, option2: newItemChecked });
            }
          }}
          itemChecked={itemChecked && itemChecked.option2}
          size={TOGGLE_SIZE.SMALL}
        />
      </div>
    </div>
  );
};
