// LabelEditable.js
import React from 'react';

/**
 * Label     
 * @param {string} label -   
 * @param {string} type -    (: 'email', 'password')
 * @param {string} value -   
 * @param {function} onChange -     
 * @returns {JSX.Element}    JSX 
 */
function LabelEditable({ className, label, type, value, onChange }) {
  return (
    <div className={`ez-labeleditable-${className ? className : '' }`}>
      <label>{label}</label>
      <input type={type} value={value} onChange={onChange} />
    </div>
  );
}

export default LabelEditable;
