import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Sidebar } from 'components/Sidebar';
import { HeaderNav } from 'components/HeaderNav';
import { AttendanceTable } from 'components/AttendanceTable';
import 'css/styles.css';

/**
 * @returns {JSX.Element}   
 * @constructor
 */

export const SheetPage = () => {
  const title = ' ';
  const { class_id } = useParams();
  const [userData, setUserData] = useState({});
  const [classData, setClassData] = useState(null); //    
  const apiUrl = process.env.REACT_APP_API_URL;

  const links = [
    { path: '/dashboard', label: '' },
    { path: '/check', label: '  ' },
    { path: '/sheet', label: ' ' },
  ];

  //    ->  
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const accessToken = localStorage.getItem('access_token');
        if (!accessToken) {
          throw new Error('Access token not found');
        }

        const response = await fetch(`${apiUrl}/api/user`, {
          method: 'GET',
          headers: {
            access: accessToken,
          },
        });

        if (!response.ok) {
          throw new Error('Failed to fetch user data');
        }

        const data = await response.json();
        setUserData(data);
      } catch (error) {
        console.error('Failed to load user data', error);
      }
    };

    fetchUserData();
  }, [apiUrl]);

  //   
  useEffect(() => {
    const fetchClassData = async () => {
      try {
        const accessToken = localStorage.getItem('access_token');
        if (!accessToken) {
          throw new Error('Access token not found');
        }

        const response = await fetch(`${apiUrl}/api/classes/${class_id}`, {
          method: 'GET',
          headers: {
            access: accessToken,
          },
        });

        if (!response.ok) {
          throw new Error('Failed to fetch class data');
        }

        const data = await response.json();
        setClassData(data);
      } catch (error) {
        console.error('Failed to load class data', error);
      }
    };

    fetchClassData();
  }, [class_id, apiUrl]);

  if (!userData || !classData) {
    return <div>Loading...</div>;
  }

  return (
    <div className="main-layout">
      <Sidebar links={links} userData={userData} classId={class_id} />
      <div className="main-content-container">
        <HeaderNav title={title} nameClass={`- ${classData.className}`} />
        <div className="main-content">
          {userData.role === '' ? (
            <div>
              {/*     */}
              <AttendanceTable classId={class_id} role={userData.role} />
            </div>
          ) : (
            <div>
              {/*     */}
              <AttendanceTable
                classId={class_id}
                role={userData.role}
                userId={userData.userId}
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
