import { useMemo } from 'react';
import { curry, reduce } from 'ramda';

export const FIELD_TYPE = {
  NUMBER: 'NUMBER',
  STRING: 'STRING',
  ARRAY: 'ARRAY',
};

export const COL_TYPE = {
  TEXT: 'TEXT',
  CHIPS: 'CHIPS',
  CUSTOM: 'CUSTOM',
};

const toPropObject = curry((propKey, array) =>
  reduce((acc, row) => ({ ...acc, [row[propKey]]: row }), {}, array),
);

/**
 *        
 *
 * @param {Object} props -   props.
 * @param {string} props.colType -  ,    .
 * @param {any} props.data -   .
 * @param {function} [props.customRender] - CUSTOM     custom render .
 * @returns {JSX.Element}       .
 */
export const Column = ({ colType, data, customRender }) => {
  switch (colType) {
    case COL_TYPE.CHIPS:
      return (
        <td>
          {data.map((row, idx) => (
            <span key={idx} className="chip">
              {data}
            </span>
          ))}
        </td>
      );
    case COL_TYPE.CUSTOM:
      return <td>{customRender(data)}</td>;
    default:
      return <td>{data}</td>;
  }
};

// const colObj = useMemo(() => toPropObject('field', colDefs), [colDefs]);

/**
 * Renders a common table component.
 *
 * @param {Object} props - The component props.
 * @param {Array} props.colDefs - An array of column definitions.
 * @param {Array} props.data - An array of data to be displayed in the table.
 * @returns {JSX.Element} The rendered table component.
 */
export const CommonTable = ({ className, colDefs, data, isIdx, idxLabel }) => {
  return (
    <table className={className}>
      <thead>
        <tr>
          {isIdx && <th>{idxLabel || 'No'}</th>}
          {colDefs.map((col, idx) => (
            <th key={col.id + idx}>{col.headerName}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {data.map((row, idx) => (
          <tr key={`${row.id}-${idx}`}>
            {isIdx && <td>{idx + 1}</td>}
            {colDefs.map((col, idxCol) => (
              <Column
                key={`${col.id}-${idxCol}`}
                colType={col.colType}
                data={row[col.field]}
                customRender={col.render}
              />
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );
};
